<template>
	<view class="edit_container">
		
			<quill-editor  v-model="content" ref="myQuillEditor" :options="editorOption" @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
				@change="onEditorChange($event)">
			</quill-editor>
	
		
	</view>
</template>
<script>
	import {
		quillEditor
	} from "vue-quill-editor"; //调用编辑器
	import 'quill/dist/quill.core.css';
	import 'quill/dist/quill.snow.css';
	import 'quill/dist/quill.bubble.css';
	import hljs from 'highlight.js'
	
	export default {
		components: {
			quillEditor
		},
		data() {
			return {
				content: ``,
				editorOption: {
					theme: 'snow',
					modules: {
						toolbar: [
							['bold', 'italic', 'underline', 'strike'],
							['blockquote', 'code-block'],
							[{
								'header': 1
							}, {
								'header': 2
							}],
							['link', 'image']
						],
						syntax: {
							highlight: text => {
								return hljs.highlightAuto(text).value; // 这里就是代码高亮需要配置的地方
							}
						}
					}
				}
			}
		},
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		    console.log(option.type); //打印出上个页面传递的参数。
		},
		methods: {
			onEditorReady(editor) { // 准备编辑器
				console.log(editor)
			},
			onEditorBlur() {}, // 失去焦点事件
			onEditorFocus() {}, // 获得焦点事件
			onEditorChange() {}, // 内容改变事件
		},
		computed: {
			editor() {
				return this.$refs.myQuillEditor.quill;
			},
		}
	}
</script>

<style scoped>
	.ql-toolbar{
		position: fixed;
		bottom: 0;
		width: calc(100% - 16px);
	}
	.ql-container.ql-snow{
		border-bottom: none;
	}
</style>